@media screen and (max-width: 768px) {
    .nav-collapse {
        background-color: green;
        
    }

    .nav-item {
        border: 1px solid black;
        padding: .7rem;
        font-size: 1rem;
        text-transform: uppercase;
        margin: 0;
    }


    .row-home {
      display: flex;
      flex-direction: column;
      padding: 4rem 0;
    }

    .home-item {
       width: 100%;
       padding: .5rem;
       margin-top: 1rem;
    }

    .benefit-row {
        padding: 1rem;
        display: flex;
        flex-direction: column;
    }

    .benefit-row img {
        width: 100%;
        margin: 2rem 0;
    }

    .benefit-title {
        font-size: 1.3rem;
        font-weight: 700;
        
    }

    .product-title-container {
        margin: 2rem 1rem;
    }

    .discover-nav {
        flex-direction: column;
     }

     .discover-link{
        text-transform: uppercase;
        padding: .5rem 0;
     }


    .card-item {
        width: 90%;
        margin-bottom: 2rem;
    }
    
    .product-title-container {
        padding: 4rem 0;
    }
 
  
    .last-div{
        padding: 1rem;
        text-align: center;
    }

    .foo-container {
        flex-direction: column;
    }

    .foo-item {
        width: 100%;
       
    }

    .foo-item h3 {
        margin-top: 2rem;
    }
   
}