*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    letter-spacing: 0.1px;
}

ul {
    list-style-type: none;
}

.area-bg {
    background: rgba(0, 0, 0, 0.6) url('./imagini/bg-image.jpg');
    min-height: 90vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-blend-mode: darken;
    
    
    
   
}

.area-bg-nav {
    background: rgba(0, 0, 0, 0.6) url('./imagini/bg-image.jpg');
    min-height: 10vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-blend-mode: darken;
   
}
.nav-toggle {
    background-color: green;
}



.nav-item {
    color: #fff;
    font-weight: 700;
    font-size: 1.2rem;
    text-decoration: none;
    margin: 0 .7rem;
}


.nav-item:hover {
    color: green !important;
}

.bg-home {
    margin-top: 9rem;
}


.f-title {
    color: green;
    font-weight: 800;
    font-size: 1.3rem;
    letter-spacing: .2rem;
}

.s-title {
font-size: 3rem;
font-weight: 700;
line-height: 1.3;
color: #fff;

}


.btn-prime {
   margin-right: 2rem;
   border-radius: 1rem;
   border: 1px solid transparent;
   padding: .6rem;
   background-color: green;
   color: #fff;
   font-weight: 700;
   
}

.btn-prime:hover{
    background-color: rgba(6, 145, 96, 0.6);
}

.btn-second {
    border-radius: 1rem;
    border: 1px solid transparent;
    padding: .7rem;
    background-color: blue;
    color: #fff;
    font-weight: 700;
    
}


/* Header */


.home-bg {
    background-color: #f5f5f5;
}


.home-content h3 {
    font-weight: 700;
}

.home-content p {
    font-size: 0.9rem;
}
.row-home {
    padding: 6rem;
}

.fas {
    display: block;
font-size: 24px;
margin-right: 15px;
color: green;
width: 65px;
height: 65px;
text-align: center;
line-height: 60px;
border: 2px green dotted;
border-radius: 999px;
}

.product-title-container {
    text-align: center;
    margin: 8rem 8rem 3rem 8rem;
}

.product-title-container h3 {
    font-size: 3rem;
}


.card-item {
    width: 15rem;
    margin: 0 1.5rem;
    box-shadow: 0 0 20px #e4e4e4;
    border-radius: 1rem;
    cursor: pointer;
    border: transparent;
    margin-bottom: 2rem;
}

.card-title {
    font-size: 1rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.card-price {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.card-img {
    padding-top: 3rem;
}

.card-button {
    padding: .8rem;
    background-color: green;
    border-radius: 1rem;
}

.fa-cart-shopping {
    margin-right: .5rem;
}

.benefit-row {
    padding: 7rem;
}

.fa-circle-check {
    color: green;
    margin-right: .7rem;
}

.benefit-title {
    font-size: 1.6rem;
    margin-bottom: .8rem;
}

.discover-nav {
    display: flex;
    justify-content: center;
}


.discover-link {
    color: #36464e;
    font-size: 1rem;
    font-weight: 700;
}
/* Home */

.review div:nth-child(1){
    display: inline-block;
    width: 150px;
    height: 120px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 1rem;
}

.review div:nth-child(2){
    margin-bottom: 3rem;
}

.review p{
    width: 70%;
    font-size: 1.3rem;
    font-weight: 700;
    margin: 0 auto;
}




.review div img {
    width: 100%;
    height: 100%;
    
}

.fa-address-book {
    width: 55px;
    border: none;
    margin-right: 0;
}

.fa-clock {
    color: green;
    width: 55px;
    height: 65px;
    text-align: center;
    line-height: 60px;
    font-size: 24px;


}

.fa-map {
    width: 55px;
    border: none;
    margin-right: 0;
}

.cnt-item div {
    display: flex;
    align-items: center;
}



.form-first {
    margin-bottom: 1rem;
}

.form-textarea {
    resize: none;
    padding: 15px;
    height: 200px;
}

.form-item {
 padding: 15px;
 margin-bottom: 1rem;
border: 1px solid #ddd;
border-radius: 3px;
}

.quest-title {
    font-size: 2.5rem;
    font-weight: 700;
}

.contact-submit {
    border-radius: 1rem;
    border: 1px solid transparent;
    padding: .6rem;
    background-color: green;
    color: #fff;
    font-weight: 700;
    margin-top: 1rem;
    width: 15%;
    text-transform: uppercase;
}

.products-header {
    display: flex;
    justify-content: center;
}

.products-header div {
    border: 2px solid;
    border-radius: 2rem;
    padding: 1rem;
    margin: 0 1rem;
    cursor: pointer;
    font-weight: 700;
}

.products-header div:hover {
    color: #fff;
    background-color: green;
    transition: 0.5s;
    border: transparent;
}

.auth-area {
    border: 2px solid black;
    width: 450px;
    margin: 0 auto;
      
}

.input-item {
    border-bottom: 1px solid;
    margin: 1rem;
}

.input-item div {
    margin-bottom: .5rem;
}

.input-item input {
    border: none;
    margin-left: .5rem;

}

.forgot-pass {
    text-align: end;
}

.log-button {
    display: block;
    width: 70%;
    margin: 0 auto;
    margin-bottom: 5rem;
    margin-top: 2rem;
    padding: .5rem;
    border-radius: 25px;
    font-size: 1.2rem;
    border: 1px solid transparent;
    background-color: green;
    color: #fff;
}

.log-button:hover {
    background-color: #7cc542;
}

.new-user {
    text-align: center;
    margin: 2rem 0;
}

/* Footer */

Footer {
    background-color: #051922;
    color: white;
}

.foo-item {
    margin-right: 5rem;
    width: 25%;
}

.foo-item h3 {
    position: relative;
    padding-bottom: 1rem;
}

.foo-item h3::after {
position: absolute;
left: 0;
bottom: 0;
width: 20px;
height: 2px;
background-color: #F28123;
content: "";

}

.foo-item div {
color: #fff;
opacity: 0.8;
font-size: 1rem;

}

.last-div {
    border-top: 1px solid;
    padding: 1rem 11rem;
    color: #fff;
opacity: 0.8;
}

/* Footer */

